<template>
      <div class="item" @click="fn">
        <span v-show="flag"><slot name="Unselected"></slot></span>
        <span v-show="!flag"><slot name="Select"></slot></span>
        <p v-text='text' :class="{active:!flag}"></p>
      </div>
</template>
<script>
  export  default {
    computed:{
      flag:function () {
        if(this.index===this.select){
          return false;
        }
        return true;
      }
    },
    props:['text','index','select'],
    methods:{
      fn:function(){
        this.$emit('momo',this.index)
      }
    }
  }
</script>
<style scoped>
  .item{
    width: 25%;
    float: left;
    text-align: center;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    padding:0.5rem 0;
  }
  img{
    width: 30%;
    margin: auto;
  }
   .active{
    color: blue;
  }
</style>



