<template>
    <div class="box">
        <router-link :to="url">
            <img src="../assets/img/icon/arrow_03.png" alt="">
      </router-link>
      <p v-text="text"></p>
       <slot name="clear"></slot>
    </div>
</template>
<script>
  export  default {
    props:['text','url']
  }
</script>
<style scoped="">
   .box img {
     transform:rotate(180deg);
     height: 20%;
     vertical-align: middle;
   }
  .box{
    color: white;
    width: 100%;
    padding: 0 3%;
    height: 5rem;
    line-height: 4.5rem;
    display: flex;
    flex-direction: row;
    text-align: center;
    background-color: blue;
  }
  p{
    margin: 0 22%;
    text-align: center;
    width: 30%;
  }
  span{
    font-size: 13px;
  }
</style>



