<template>
  <div id="app">
    <router-view/>
    <div class="foot">
       <router-link to="/home">
         <FooterTabBar text='首页' index="/home"  @momo="fn" :select="select" >
           <img src="./assets/img/icon/footer_0322_05.png" alt="" slot="Unselected">
           <img src="./assets/img/icon/footer_0322_01.png" alt="" slot="Select">
         </FooterTabBar>
       </router-link>
       <router-link to="/destination">
         <FooterTabBar text='目的地' index="/destination" @momo="fn" :select="select" >
           <img src="./assets/img/icon/footer_0322_06.png" alt="" slot="Unselected">
           <img src="./assets/img/icon/footer_0322_02.png" alt="" slot="Select">
         </FooterTabBar>
       </router-link>
      <router-link to="/history">
        <FooterTabBar text='浏览历史' index="/history"  @momo="fn" :select="select">
          <img src="./assets/img/icon/footer_0322_07.png" alt="" slot="Unselected">
          <img src="./assets/img/icon/footer_0322_03.png" alt="" slot="Select">
        </FooterTabBar>
      </router-link>
      <router-link to="/personal">
        <FooterTabBar text='个人' index="/personal" @momo="fn" :select="select" >
          <img src="./assets/img/icon/footer_0322_08.png" alt="" slot="Unselected">
          <img src="./assets/img/icon/footer_0322_04.png" alt="" slot="Select">
        </FooterTabBar>
      </router-link>
    </div>
  </div>
</template>
<script>
  import FooterTabBar from './components/footerTabBar.vue'
  export default{
    data:function (){
      return{
        select:'1'
      }
      },
    components:{
     FooterTabBar
  },
    methods:{
      fn:function (val) {
        this.select =val;
      }
    },
    created:function () {
//      this.$router.push('/home');
      this.select =window.location.hash.substr(1);
    }
}
</script>
<style>
#app {

}
  *{
    margin: 0;
    padding: 0;
  }
  ul{
    list-style: none;
  }
  a{
    color: #999;
  }
  html{
    font-size: 16px;
    color: #666;
    overflow-x: hidden;
  }
.foot{
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #e3e3e3;
}
  input{
    outline: none;
    border: none;
  }
input:-ms-input-placeholder{
  color: #f1f1f1;
}
input::-webkit-input-placeholder{
  color: #f1f1f1;
}
input::-moz-placeholder{
  color: #f1f1f1;
}
</style>



