<template>
  <div>
    <header>
      <Heads text="个人" url="/history"></Heads>
    </header>
    <section>
      <div class="user">
        <span></span>
      </div>
       <div class="user-info">
          <div class="personal-info">
            <h4>您好 游客</h4>
            <p>世界很大，抬脚出去荡~</p>
          </div>
         <div class="integration">
           <div class="item">
              <h4>￥<span>50</span></h4>
               <p>红包优惠券</p>
           </div>
             <div class="item">
               <h4><span>1000</span>分</h4>
               <p>积分</p>
             </div>
           </div>
         </div>
      <div>
       </div>
        <ul>
          <router-link to="/personal/allorders">
          <li>
               <img src="../assets/img/icon/1.gif" alt="" >
               <p>全部订单</p>
          </li>
      </router-link>
          <li>
               <img src="../assets/img/icon/2.gif" alt="" >
               <p>待付款</p>
          </li>
          <li>
               <img src="../assets/img/icon/3.gif" alt="" >
               <p>未出行</p>
          </li>
          <li>
               <img src="../assets/img/icon/4.gif" alt="" >
               <p>待点评</p>
          </li>
        </ul>
      <div class="items" v-for="item in arr">
        <div>
          <img :src="item.img" alt="">
          <span v-text="item.text"></span>
        </div>
         <div class="arrow">
           <img src="../assets/img/icon/arrow_03.png" alt="" >
         </div>
      </div>
    </section>
    <router-view></router-view>
  </div>
</template>
<script>
  import Heads from '../components/heads.vue'
  import FooterTabBar from '../components/footerTabBar.vue'
  export  default {
    components:{
      Heads,
      FooterTabBar
    },
    data:function () {
      return{
        arr:[
          {
            text:'我的相册',
            img:require('../assets/img/icon/5.gif')
          },
          {
            text:'我的行程',
            img:require('../assets/img/icon/6.gif')
          },
          {
            text:'我的游记',
            img:require('../assets/img/icon/7.gif')
          },
          {
            text:'我的财富',
            img:require('../assets/img/icon/8.gif')
          },
        ]
      }
    }
  }
</script>

<style scoped="">
  ul li{
    width: 25%;
    float: left;
    text-align: center;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    padding:0.5rem 0;
  }
  li img{
    width: 45%;
    margin: auto;
  }
   .user-info{
     background-color: #eaeaea;
     margin-top: -5.5rem;
     border-bottom: 1px solid #ccc;
   }
  .user{
    background-color: #e9e9e9;
    width: 7.5rem;
    height: 7.5rem;
    border-radius: 50%;
    border: 3px solid #fff;
    position: relative;
    /*top:-15%;*/
    /*left: 35%;*/
   margin:auto;
    margin-top: -2.5rem;
    z-index: 9;
    transform: scale(0.7)
  }
  .user span{
    background: url("../assets/img/icon/sprites.png") no-repeat -502px -790px;
    display: block;
    width: 100%;
    height: 76%;
  }
  .personal-info{
    text-align: center;
    padding-top: 5rem;
  }
   .personal-info p{
     font-size: 13px;
   }
  .integration{
    display: flex;
    flex-direction: row;
    width: 100%;
  }
   .integration .item:nth-of-type(1){
     border-right: 2px solid #ccc;
   }
   .integration .item{
     width: 50%;
     text-align: center;
     margin: 1rem 0;
     /*box-sizing: border-box;*/
   }
   .integration .item h4{
     font-weight: 500;
   }
   .integration .item h4 span{
     font-size: 24px;

   }
   .integration .item p{
     font-size: 13px;
   }
  .items{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 90%;
    padding: 1rem;
    height: 2rem;
    margin: auto;
  }
  .items div img{
    max-width: 30%;
    vertical-align:middle;
   }
</style>



