import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home.vue'
import Destination from '../pages/destination.vue'
import History from '../pages/history.vue'
import Personal from '../pages/personal.vue'
import Hot from '../components/hot.vue'
import Domestic from '../components/domestic.vue'
import Abroad from '../components/abroad.vue'
import Alloeders from '../components/allorders.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Home
    },
    {
      path:'/home',
      name:'Home',
      component:Home
    },
    {
      path:'/destination',
      name:'Destination',
      component: Destination,
      children:[
        {path:'',component:Hot},
        {path:'hot',component:Hot},
        {path:'domestic',component:Domestic},
        {path:'abroad',component:Abroad}
      ]
    },
    {
      path:'/history',
      name:'History',
      component:History
    },
    {
      path:'/personal',
      name:'Personal',
      component: Personal
    },{
      path:'/personal/allorders',
      name:'Alloeders',
      component:Alloeders
    }
  ]
})



