<template>
  <div>
    <header>
      <Heads text="选择目的地" url="/home"></Heads>
      <Inputs></Inputs>
    </header>
     <section>
      <div class="content">
        <div class="left-content">
          <router-link :to="item.url" v-for="item in arr">
              <div class="left-item" @click="fn(item.url)" :class="{active:select==item.url}">
                <span>
                  <img :src="item.unselect" alt="" v-show="select!=item.url">
                  <img :src="item.select" alt="" v-show="select==item.url">
                </span>
                <p v-text="item.text"></p>
              </div>
          </router-link>
        </div>
        <div class="right-content">
          <router-view></router-view>
       </div>
      </div>
     </section>
  </div>
</template>
<script>
   import Heads from '../components/heads.vue'
   import  Inputs from '../components/inputs.vue'
   export  default {
     data:function () {
       return{
        arr:[{
            url:'/destination/hot',
            text:'热门',
            unselect:require('../assets/img/icon/sprites_03.png'),
            select:require('../assets/img/icon/sprites_12.png')
          },
          {
            url:'/destination/domestic',
            text:'国内',
            unselect:require('../assets/img/icon/sprites_05.png'),
            select:require('../assets/img/icon/sprites_13.png')
          },
          {
            url:'/destination/abroad',
            text:'国外',
            unselect:require('../assets/img/icon/sprites_07.png'),
            select:require('../assets/img/icon/sprites_14.png')
          }]
       }
     },
     select:'/',
     methods:{
       fn:function(url) {
         this.select = url;
       }
     },
     components:{
       Heads,
       Inputs
     },
     created:function () {
       this.$router.push('/destination/hot');
       this.select =window.location.hash.substr(1)
     }
   }
</script>
<style scoped="">
   .content{
     display: flex;
     flex-direction: row;
     width: 100%;
   }
   .left-content{
     width: 20%;
     background-color: #f1f1f1;
   }
   .right-content{
     width: 80%;
   }
   .content .left-content .left-item{
     height: 5.5rem;
     text-align: center;
     font-size: 18px;
     padding: 0.5rem 0;
   }
   a{
     text-decoration: none;
     color: #999;
   }
   p{
     margin-top: -0.5rem;
   }
   .content .left-content .left-item span img{
     max-width: 90%;
   }
   .active{
     background-color: #fff;
     border-left: 3px solid orange;
     color: blue;
   }
</style>



